/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
// import ServerSideRender from '@wordpress/server-side-render';
// import { useState } from '@wordpress/element';
import {
	//InspectorControls,
	BlockControls,
	useBlockProps,
} from '@wordpress/block-editor';
import {
	//PanelBody,
	SelectControl,
} from '@wordpress/components';

/**
 * @Internal dependencies
 */
import { arkbGlbStr } from '@helper';
import icon from './icon';
import metadata from './block.json';
import { ArkbMarginControl } from '@components';

/**
 * 設定項目
 */
const targetOptions = [
	{
		label: __('Default', 'arkhe-blocks'),
		value: '',
	},
	{
		label: 'H2',
		value: 'h2',
	},
	{
		label: 'H3',
		value: 'h3',
	},
	{
		label: 'H4',
		value: 'h4',
	},
];

/**
 * Register Block
 */
const blockName = 'ark-block-toc';
registerBlockType(metadata.name, {
	title: __('Table of Contents', 'arkhe-blocks'),
	icon,
	edit: ({ attributes, setAttributes }) => {
		const { target } = attributes;

		const blockProps = useBlockProps({
			className: blockName,
		});

		return (
			<>
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
				</BlockControls>
				{/* <InspectorControls>
					<PanelBody title={__('Settings', 'arkhe-blocks')} initialOpen={true}>
						<SelectControl
							value={target || ''}
							options={targetOptions}
							onChange={(val) => {
								setAttributes({ target: val || undefined });
							}}
						/>
					</PanelBody>
				</InspectorControls> */}
				<div {...blockProps}>
					<div className='ark-block-toc__title'>
						{__('Table of Contents', 'arkhe-blocks')}
					</div>
					<div className='ark-block-toc__description arkb-helpText'>
						{__('The Table of Contents is displayed here.', 'arkhe-blocks')}
					</div>
					<div className='ark-block-toc__settings'>
						<SelectControl
							label={__('What heading level to display', 'arkhe-blocks')}
							value={target || ''}
							options={targetOptions}
							onChange={(val) => {
								setAttributes({ target: val || undefined });
							}}
							help={
								arkbGlbStr('NOTE') +
								__(
									'Headings with the "arkb-toc-off" class are not displayed.',
									'arkhe-blocks'
								)
							}
						/>
					</div>
				</div>
			</>
		);
	},
	save: () => {
		return null;
	},
});
